html {
  body {
    .testimonial-slider {
      .quote {
        cursor: pointer;
        font-family: 'Didot';
        font-style: italic;
        font-size: 2rem;
        font-weight: 300;
        line-height: 1.4;
        margin-bottom: 20px; 
      }
      h4 {
        font-size: .875rem;
      }
      @media all and (max-width: 767px) {
        .quote {
          font-size: 1.2rem;
        }
      }
    }
  }
}